import React, { Component } from 'react';
import Button3D from '../button3D/Button3D';

export default class Slide extends Component {
  constructor(props) {
    super(props);
   	this.state = { infoClasses: "info" }
    this.switchText = this.switchText.bind(this);
    this.visit = this.visit.bind(this);
    this.preloaded = false;
  }

  componentDidMount() {
  	if(this.props.index === 0) {
  		setTimeout(() => { this.refs[0].classList.add('slide-reset'); }, 1500);
  	}
  }

  componentWillReceiveProps({ width, selectedIndex, index }) {
  	if(width >= 957 && this.state.infoClasses === "info change-info") {
  		this.setState({ infoClasses: "info" });
  	}
  	setTimeout(() => {
  		if(selectedIndex === index) {
  			this.refs[index].classList.add('slide-reset');
  		} else {
  			this.refs[index].classList.remove('slide-reset');
  			this.setState({ infoClasses: "info" });
  		}
	  }, 1500);
  }

  shouldComponentUpdate({ index, selectedIndex, moveX, width, height }, { infoClasses }) {
  	const curProps = this.props;
  	if(infoClasses !== this.state.infoClasses) return true;
  	else if(selectedIndex !== curProps.selectedIndex) return true;
  	else if(moveX !== curProps.moveX && selectedIndex === index) return true;
  	else if(width !== curProps.width) return true;
    else if(height !== curProps.height) return true;
  	return false;
  }

  visit(e) {
    window.open(e.target.dataset.url, '_blank');
  }

  switchText() {
  	this.setState(({ infoClasses }) => {
  		return {
  			infoClasses: infoClasses === "info" ? "info change-info" : "info"
  		}
  	});
  }

  render() {
  	const { index, selectedIndex, moveX, name, width, height, url, p1, p2 } = this.props;
    const isSelected = selectedIndex === index;
    return (
    	<div
				ref={index}
				className={isSelected ? "project is-selected" : "project"}
        id="slide"
				style={{
					backgroundPositionX: isSelected ? `${moveX}%` : '50%',
          height,
          width
				}}>
				<div 
          className="center-stuff"
          style={{ height, width }}>
          <div>
            <h2>
              {
                name.split('').map((letter, i) => {
                  return (letter === ' ') ? <div key={i}>&nbsp;&nbsp;&nbsp;</div> : <div style={{transitionDelay: (0.3 + i/20) + 's'}} key={i}>{letter}</div>
                })
              }
            </h2>
            <div className={this.state.infoClasses}>
              <p>
                {p1}
                <a onClick={this.switchText}>&nbsp;More Info</a>
              </p>
              <p className={width < 957 ? "text-switch" : ""}>
                {p2}
                <a onClick={this.switchText}>&nbsp;Back</a>
              </p>
            </div>
            <Button3D 
              function={this.visit}
              url={url}
              text="VISIT" />
          </div>
        </div>
			</div>
    );
  }
}



