import React, { Component } from 'react';
import Burger from './Burger';

export default class Header extends Component {

	shouldComponentUpdate({ burgerStuff }) {
		return burgerStuff !== this.props.burgerStuff;
	}

	render() {
		const { nav, burger, burgerStuff } = this.props;
		return (
			<header className="header">
				<div>
					<h1
						data-page="Home"
						onClick={nav}>
						{
							'FIG'.split('').map(letter => {
								return (
									<div 
										data-page="Home"
										onClick={nav}
										key={letter}>{letter}</div>
								);
							})
						}
					</h1>
					<Burger 
						classes={burgerStuff} 
						burger={burger} />
				</div>
			</header>
		);
	}
}


