import React, { Component } from 'react';
import $ from 'jquery';
import ripples from 'ripples';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Home extends Component {
	
	componentDidMount() {
		const { pastInitLoad, loaded } = this.props;
		if(pastInitLoad) setTimeout(loaded, 500);
		$('#home').ripples({
        resolution: 512,
        dropRadius: 10, 
        perturbance: 0.02,
    });
	}

	componentWillUnmount() {
		this.props.removeHomeClasses();
		$('#home').ripples('destroy');
	}

	shouldComponentUpdate({ classes, transDur, moveX }) {
		const curProps = this.props;
		if(classes !== curProps.classes) return true;
		else if(transDur !== curProps.transDur) return true;
		else if(moveX !== curProps.moveX) return true;
		return false;
	}

	render() {
		const { handlePageMove, handlePageEnter, classes, transDur, nav, moveX } = this.props;
		let inc = 7, inc2 = 0;
		return (
			<section 
				id="home"
				onMouseMove={handlePageMove}
				onMouseEnter={handlePageEnter}
				onTouchMove={handlePageMove}
				onTouchStart={handlePageEnter}
				className={classes}
				style={{
					backgroundPosition: `${moveX}% 100%`,
					transition: `background-position ${transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText text="Alex" />
				<BackgroundText text="Figliolia" />
				<div>
					<div>
						<h2>
							{
								"Full Stack".split('').map((letter, i) => {
									if(letter === ' ') {
										return (
											<div 
												key={i}
												className="space"
												style={{
													transitionDelay: inc/140 + 's'
												}}>&nbsp;&nbsp;</div>
										);
									} else {
										inc-=i;
										return (
											<div 
												key={i}
												style={{
													transitionDelay: inc/140 + 's'
												}}>{letter}</div>
										);
									}
								})
							}
						</h2>
						<h2>
							{
								'Developer'.split('').map((letter, i) => {
									inc2 += i;
									if(letter === ' ') {
										return (
											<div 
												key={i}
												className="space"
												style={{
													transitionDelay: inc2/140 + 's'
												}}>&nbsp;&nbsp;</div>
										);
									} else {
										inc-=i;
										return (
											<div 
												key={i}
												style={{
													transitionDelay: inc2/140 + 's'
												}}>{letter}</div>
										);
									}
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Work"
						function={nav} />
				</div>
			</section>
		);
	}
}



