import React, { Component } from 'react';
import PageSwitch from 'pageswitch';
import API from './workApi';
import Slide from './Slide';

export default class Work extends Component {
	constructor(props) {
		super(props);
		this.state = { selectedIndex: 0 };
		this.pw = null;
	}

	componentDidMount() {
		const desktop = this.props.width > 957;
  	this.pw = new PageSwitch('slides', {
			duration: 750,
			direction: desktop ? 1 : 0,
	    start: 0,
	    loop: true,
	    ease: 'ease',
	    transition: desktop ? 'flip3d' : 'flip3dY',
			freeze: false,
			mouse: true,
		  mousewheel: true,
			arrowkey: true,
		  autoplay: false,
			interval: false
		});
		this.pw.on('after', () => {
			this.setState({ selectedIndex: this.pw.current });
		});
  }

  componentWillUnmount() {
  	if(this.pw) {
  		this.pw.destroy();
  		this.pw = null;
  	}
  }

  shouldComponentUpdate({ moveX, height, width }, { selectedIndex }) {
  	const curProps = this.props;
  	if(moveX !== curProps.moveX) return true;
  	else if(selectedIndex !== this.state.selectedIndex) return true;
  	else if(height !== curProps.height) return true;
  	else if(width !== curProps.width) return true;
  	return false;  
  }

  componentDidUpdate(prevProps) {
  	const { width } = this.props;
  	if(prevProps.width !== width && this.pw) 
  		this.pw.setTransition(width > 957 ? 'flip3d' : 'flip3dY'); 
  }

	render() {
		const { selectedIndex } = this.state;
		const { moveX, width, height } = this.props; 
		return (
			<div className='work-wrapper' style={{height, width}}>
				<div style={{ height, width, perspective: '1000px' }}>
					<section className="work" style={{height, backgroundColor: '#fff'}} id="slides">
						{
							API.map((obj, i) => {
								const { name, p1, p2, url } = obj; 
								return (
									<Slide
										index={i}
										key={i}
										name={name}
										selectedIndex={selectedIndex}
										moveX={moveX}
										width={width}
										height={height}
										url={url}
										p1={p1}
										p2={p2} />
								);
							})
						}
					</section>
				</div>
			</div>
		);
	}
}


