import React, { Component } from 'react';
import $ from 'jquery';
import ripples from 'ripples';

export default class Menu extends Component {
	constructor(props) {
	  super(props);
		this.onTouchStart = this.onTouchStart.bind(this);
		this.onTouchEnd = this.onTouchEnd.bind(this);
	}

	componentDidMount() {
		$('#arrow').ripples({
        resolution: 512,
        dropRadius: 10, 
        perturbance: 0.02,
    });
	}

	shouldComponentUpdate({ classes }) {
		return classes !== this.props.classes;
	}

	onTouchStart({ target }) {
		target.classList.add('active');
	}

	onTouchEnd({ target }) {
		target.classList.remove('active');
	}
	
	render() {
		const { classes, page, nav } = this.props;
		const d = classes === "menu" ? 0 : 0.3;
		return (
			<section className={classes}>
{/*				<div className="panel panel1"></div>
				<div className="panel panel2"></div>*/}
				<div className="arrow" id="arrow"></div>
				<div className='links'>
					<div 
						className={page === 'Home' ? 'link on-page' : 'link'}
						onTouchStart={this.onTouchStart}
						onTouchEnd={this.onTouchEnd}
						onClick={nav}  
						data-page="Home">
						{
							'Home'.split('').map((letter, i) => {
								return (
									<div
										key={letter}
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Home'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
					<div 
						className={page === 'Work' ? 'link on-page' : 'link'}
						onTouchStart={this.onTouchStart}
						onTouchEnd={this.onTouchEnd}
						onClick={nav}
						data-page="Work">
						{
							'Work'.split('').map((letter, i) => {
								return (
									<div
										key={i} 
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Work'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
					<div 
						className={page === 'Contact' ? 'link on-page' : 'link'}
						onTouchStart={this.onTouchStart}
						onTouchEnd={this.onTouchEnd}
						onClick={nav}
						data-page="Contact">
						{
							'Contact'.split('').map((letter, i) => {
								return (
									<div
										key={i}
										className='link-letter'
										style={{
											transitionDelay: (d + (i/20)) + 's'
										}}>{letter}</div>
								);
							})
						}
						<div className='inner'>
							{
								'Contact'.split('').map((letter, i) => {
									return (
										<div
											key={i}
											style={{
												transitionDelay: (d + (i/20)) + 's'
											}}>{letter}</div>
									);
								})
							}
						</div>
					</div>
				</div>
			</section>
		);
	}
}


