import React, { Component } from 'react';
import $ from 'jquery';
import ripples from 'ripples';
import Button3D from '../button3D/Button3D';
import BackgroundText from '../backgroundText/BackgroundText';

export default class Contact extends Component {
	constructor(props) {
	  super(props);
		this.sendEmail = this.sendEmail.bind(this);
		this.gitHub = this.gitHub.bind(this);
	}

	componentDidMount() {
		setTimeout(this.props.loaded, 500);
		$('#contact').ripples({
        resolution: 512,
        dropRadius: 10, 
        perturbance: 0.02,
    });
	}

	componentWillUnmount() {
		this.props.removeContactClasses();
		$('#contact').ripples('destroy');
	}

	shouldComponentUpdate({ classes, transDur, moveX }) {
		const curProps = this.props;
		if(classes !== curProps.classes) return true;
		else if(transDur !== curProps.transDur) return true;
		else if(moveX !== curProps.moveX) return true;
		return false;
	}

	sendEmail() {
		window.location.href = "mailto:alexfigliolia@gmail.com.com?subject=Subject";
	}

	gitHub() {
		window.open('https://github.com/alexfigliolia', '_blank');
	}

	render() {
		const { handlePageMove, handlePageEnter, classes, moveX, transDur } = this.props;
		return (
			<section 
				id="contact"
				onMouseMove={handlePageMove}
				onMouseEnter={handlePageEnter}
				onTouchMove={handlePageMove}
				onTouchStart={handlePageEnter}
				className={classes}
				style={{
					backgroundPosition: `${moveX}% 100%`,
					transition: `background-position ${transDur} 0s, transform 0.5s 0s`
				}}>
				<BackgroundText text="Alex" />
				<BackgroundText text="Figliolia" />
				<div>
					<div>
						<h2>
							{
								'Contact'.split('').map((letter, i) => {
									return (
										<div
											style={{
												transition: `transform 0.5s ${i/20}s`
											}} 
											key={i}>{letter}</div>
									);
								})
							}
						</h2>
					</div>
					<Button3D 
						text="Call Me"
						function={(null)} />
					<Button3D 
						text="Email Me"
						function={this.sendEmail} />
					<Button3D 
						text="GitHub"
						function={this.gitHub} />
				</div>
			</section>
		);
	}
}


